body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    font-family:'Montserrat';
    overflow-x:hidden;
}
a.navbar-brand{
    font-weight:900;
}

a.nav-item{
    font-weight:600;
}

@media screen and (max-width: 991px){
    .nav-search{
        display:none;
    }
}
.side-panel{
    min-height:70vh;
    padding-right:0;
}

.side-panel ul li{
    list-style-type:none;
    border-bottom:1px solid #80aee2;
}

.side-panel ul li a{
    text-decoration:none;
    color:#fff;
    transition:all 0.25s ease-out;
}

.side-panel ul li a:hover{
    color:#80aee2;
    margin-left:10px;
    transition:all 0.125s ease-in;
}

@media screen and (max-width:767px){
    .side-panel{
        display:none;
    }
}
.main-app{
    min-height:70vh;
    margin:4rem 0;
}
.app-header span{
    font-size:2.5rem;
    font-weight:900;
}

.brief-text{
    font-weight:600;
}

@media screen and (max-width:767px){
    .app-header span{
        font-size:2rem;
        line-height: 1.25;
    }

    .brief-text{
        font-size:0.875rem;
    }
}
.target-list{
    padding:0;
}

.companies-list{
    list-style-type:none;
    margin:0;
    padding:0;
}

.companies-list li{
    transition:0.25s ease-out all;
}

.companies-list li:hover{
    background:#80aee2;
    padding-left:10px;
    transition:0.125s ease-in all;
}

.companies-list li a{
    font-size:1.5rem;
    transition:0.25s ease-out all;
}

.companies-list li a:hover{
    text-decoration:none;
}
.form-body{
    width:85%;
    margin:0 auto;
}

@media screen and (max-width:767px){
    .form-body{
        width:95%;
    }
}
h2{
    font-weight:900;
    font-size:3rem;
}
.page-footer{
    color:#fff;
}

.page-footer h5{
    font-weight:700;
}

.list-unstyled li a{
    color:#999;
    font-weight:600;
}

.list-unstyled li a:hover{
    color:#ddd;
}
